@import url(https://db.onlinewebfonts.com/c/02cbb7a31cfe23290e89f1316ea8886f?family=October+Crow);
@import url(https://db.onlinewebfonts.com/c/800249dca51965ef8adbfa527c330d6f?family=Teen);
@import url(https://db.onlinewebfonts.com/c/02cbb7a31cfe23290e89f1316ea8886f?family=October+Crow);
@import url(https://db.onlinewebfonts.com/c/800249dca51965ef8adbfa527c330d6f?family=Teen);
.header {
  font-size: 18px;
  line-height: 167%;
  color: #e4f357;
  background: transparent;
  position: relative;
  z-index: 10;
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__menu-list-wrapper ul {
  display: flex;
  gap: 72px;
  list-style: none;
}
.header__button {
  font-family: "Teen", sans-serif;
  font-weight: 700;
  font-size: 16px;
  color: #1f261d;
  border: none;
  border-radius: 4px;
  padding: 10px 32px;
  margin-left: 52px;
  width: 115px;
  height: 40px;
  background: linear-gradient(180deg, #e4f357 0%, #a6b22e 100%);
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a {
  text-decoration: none;
  font: inherit;
  color: inherit;
  cursor: pointer;
}

body {
  color: red;
  font-family: "Teen", sans-serif;
  font-weight: 400;
}

section {
  padding: 150px 0;
}

h5 {
  font-family: "October Crow", sans-serif;
  font-weight: 400;
  font-size: 51px;
  line-height: 120%;
  color: #e4f357;
  margin-bottom: 20px;
}

p {
  font-family: "Teen", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 150%;
  color: #9A998E;
}

.container {
  max-width: 1170px;
  margin: 0 auto;
}

.hero-background-wrapper {
  background: linear-gradient(315deg, #223114 0%, #6a9a3c 13.74%, #537433 29.32%, #1e2b13 55.21%, #000 100%);
  padding-top: 36.5px;
  position: relative;
}
.hero-background-wrapper__background-img {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
}

.hero-section {
  position: relative;
  z-index: 2;
  padding: 0;
}
.hero-section__content {
  width: 700px;
  margin-top: 241.5px;
  padding-bottom: 325px;
}
.hero-section__text {
  text-transform: uppercase;
  font-family: "October Crow", sans-serif;
  font-weight: 400;
  font-size: 102px;
  line-height: 98%;
  background: linear-gradient(360deg, #a0eb00 0%, #e4f357 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 60px;
}
.hero-section__buttons__calculator {
  font-family: "Teen", sans-serif;
  font-weight: 700;
  font-size: 18px;
  color: #1f261d;
  border: none;
  border-radius: 4px;
  padding: 24px 32px;
  width: 203px;
  height: 70px;
  background: linear-gradient(180deg, #e4f357 0%, #a6b22e 100%);
  margin-right: 30px;
}
.hero-section__buttons__read {
  font-family: "Teen", sans-serif;
  font-weight: 700;
  font-size: 18px;
  background: linear-gradient(180deg, #e4f357 0%, #a6b22e 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  border: 1px solid #e4f357;
  border-radius: 4px;
  padding: 24px 32px;
  width: 158px;
  height: 70px;
}

.home-section {
  background-color: #0A1D03;
}
.home-section__flex-wrapper {
  display: flex;
  gap: 100px;
  padding: 0 50px;
}
.home-section__flex-wrapper__description {
  margin: auto 0 auto 30px;
}
.home-section__flex-wrapper__description p {
  margin-bottom: 60px;
}
.home-section__flex-wrapper__description button {
  font-family: "Teen", sans-serif;
  font-weight: 700;
  font-size: 18px;
  color: #1f261d;
  border: none;
  border-radius: 4px;
  padding: 16px 32px;
  width: 158px;
  height: 54px;
  background: linear-gradient(180deg, #e4f357 0%, #a6b22e 100%);
  margin-right: 30px;
}

.new-games-section {
  background-color: #1f261d;
  text-align: center;
}
.new-games-section__description {
  width: 470px;
  margin: 0 auto 60px;
}
.new-games-section__cards-container {
  display: flex;
  flex-wrap: wrap;
  column-gap: 30px;
  row-gap: 40px;
  justify-content: center;
}
.new-games-section__card {
  border-radius: 20px;
  padding: 40px 0px;
  width: 370px;
  background-color: #0A1D03;
}
.new-games-section__card-title {
  font-family: "Teen", sans-serif;
  font-weight: 400;
  font-size: 28px;
  line-height: 125%;
  text-align: center;
  color: #e4f357;
  margin-bottom: 30px;
}
.new-games-section__card-text {
  padding: 30px 40px 30px;
}
.new-games-section__card-parameter-value {
  display: flex;
  justify-content: space-between;
}
.new-games-section__card-parameter-value p {
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
}
.new-games-section__card-parameter-value p:not(:first-child) {
  color: #fff;
}
.new-games-section__card-link {
  text-align: center;
  font-family: "Teen", sans-serif;
  font-weight: 700;
  font-size: 16px;
  color: #1f261d;
  border-radius: 4px;
  width: 310px;
  margin: 0 auto;
  padding: 14px 0;
  background: linear-gradient(180deg, #e4f357 0%, #a6b22e 100%);
}

.magic-section {
  background-color: #0A1D03;
}
.magic-section__flex-wrapper {
  display: flex;
  gap: 100px;
  padding: 0 50px;
}
.magic-section__flex-wrapper__description {
  margin: auto 30px auto 0;
}
.magic-section__flex-wrapper__description p {
  margin-bottom: 60px;
}
.magic-section__flex-wrapper__description button {
  font-family: "Teen", sans-serif;
  font-weight: 700;
  font-size: 18px;
  color: #1f261d;
  border: none;
  border-radius: 4px;
  padding: 16px 32px;
  width: 158px;
  height: 54px;
  background: linear-gradient(180deg, #e4f357 0%, #a6b22e 100%);
  margin-right: 30px;
}

.best-section {
  background-color: #1f261d;
  text-align: center;
}
.best-section__description {
  width: 470px;
  margin: 0 auto 60px;
}
.best-section__cards-container {
  display: flex;
  flex-direction: column;
  gap: 30px;
}
.best-section__card {
  display: flex;
  gap: 60px;
  background-color: #0A1D03;
  padding: 70px;
  border-radius: 20px;
}
.best-section__card__text-features-container {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 66px;
  column-gap: 40px;
  row-gap: 10px;
  margin-bottom: 50px;
}
.best-section__card__text-feature {
  display: flex;
  gap: 15px;
  align-items: center;
}
.best-section__card svg {
  margin: 4px;
}
.best-section__card p {
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
}
.best-section__card__buttons-container {
  display: flex;
  gap: 30px;
  align-items: center;
}
.best-section__card__view-more {
  text-align: center;
  font-family: "Teen", sans-serif;
  font-weight: 700;
  font-size: 16px;
  color: #1f261d;
  border-radius: 4px;
  width: 146px;
  padding: 14px 0;
  background: linear-gradient(180deg, #e4f357 0%, #a6b22e 100%);
}
.best-section__card__reviews {
  font-weight: 700;
  font-size: 16px;
  background: linear-gradient(180deg, #e4f357 0%, #a6b22e 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-left: 32px;
}
.best-section__card__text h4 {
  font-family: "Teen", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 128%;
  color: #e4f357;
  text-align: left;
  margin-bottom: 30px;
}

.how-to-play {
  background-color: #1f261d;
  padding-top: 0;
}
.how-to-play__container {
  max-width: 1500px;
  margin: 0 auto;
}
.how-to-play__video-wrapper {
  position: relative;
  width: 100%;
  height: 600px;
  background: linear-gradient(315deg, #223114 0%, #6a9a3c 13.74%, #537433 29.32%, #1e2b13 55.21%, #000 100%);
  opacity: 0.7;
  color: white;
  text-align: center;
}
.how-to-play__video-wrapper h5 {
  margin-bottom: 30px;
}
.how-to-play__video-wrapper p {
  color: #fff;
  font-weight: 400;
  width: 570px;
  margin: 0 auto;
}
.how-to-play__play-button {
  margin: 155px 0 60px;
  background: transparent;
  border: none;
  cursor: pointer;
}
.how-to-play__video-wrapper video {
  display: none;
  width: 100%;
}

.faqs-section {
  background-color: #0A1D03;
  text-align: center;
}
.faqs-section__description {
  margin-bottom: 60px;
}
.faqs-section__description p {
  max-width: 470px;
  font-weight: 400;
  line-height: 150%;
  margin: 20px auto 0;
}
.faqs-section__qa-block {
  display: flex;
  flex-direction: column;
  gap: 20px;
  text-align: left;
  max-width: 715px;
  margin: 0 auto;
}
.faqs-section__qa-block-item {
  background: #1f261d;
  border-radius: 4px;
  padding: 15px 20px;
}
.faqs-section__qa-block-question {
  display: flex;
  justify-content: space-between;
}
.faqs-section__qa-block-question p {
  color: #e4f357;
}
.faqs-section__qa-block-answer {
  display: none;
  margin-top: 30px;
}
.faqs-section__arrow-icon {
  transition: transform 0.3s ease;
}
.faqs-section__arrow-icon.rotate {
  transform: rotate(180deg);
}
.faqs-section__arrow-icon.rotate path {
  fill: #e4f357;
}

.stories-section {
  background-color: #1f261d;
  text-align: center;
}
.stories-section__description {
  margin-bottom: 60px;
}
.stories-section__description p {
  max-width: 470px;
  font-weight: 400;
  line-height: 150%;
  margin: 20px auto 0;
}
.stories-section__stories-container {
  display: flex;
  gap: 30px;
  text-align: left;
}
.stories-section__item {
  background: #0A1D03;
  border-radius: 20px;
  width: calc(50% - 15px);
  padding: 50px 40px;
}
.stories-section__item__story {
  font-weight: 400;
  font-size: 24px;
  margin-bottom: 30px;
}
.stories-section__item__user-info-container {
  display: flex;
  gap: 20px;
}
.stories-section__item__user-info-container img {
  width: 70px;
  height: 70px;
  border-radius: 100%;
}
.stories-section__item__user-info-container__name {
  font-weight: 400;
  font-size: 18px;
  line-height: 167%;
  color: #e4f357;
}
.stories-section__item__user-info-container__level {
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
}